import React, { useState } from 'react';

const Search = ({ placeholder, inputLabel }) => {
    const [input, setinput] = useState('');

    const handleSearch = (e) => {
        e.preventDefault();
        console.log(input)
    };

    const handleInputChange = (e) => {
        setinput(e.target.value);
    };

    return (
        <form onSubmit={handleSearch}>
            <label className='text-secondary mx-1 my-2' htmlFor="input handle">{inputLabel}</label>

            <div className="input-group mb-3 " id='findretailer_input'>

                <input type="text" value={input}  onChange={handleInputChange} name="inputvalue" className="form-control rounded-0" required placeholder={`Enter ${placeholder}`} />
                <button className="btn rounded-0" type="submit" style={{ background: '#F3CA3E' }}>
                    <i className="fa-solid fa-magnifying-glass text-dark"></i>
                </button>

            </div>
        </form>
    );
};

export default Search;
