import React, { useEffect, useState } from 'react'

import Search from "./searchinput/Search";

const Map = React.lazy(() => import("../../../../components/map/Map"));
const MapSection = React.lazy(() => import("../mapsection/MapSection"));

const FindRetailer = () => {

  const [isretailer, setIsRetailer] = useState(false);
  const [retailer, setRetailer] = useState("text-secondary");
  const [inputlabel, setInputLabel] = useState("Postcode or suburb");
  const [placeholder, setPlaceHolder] = useState("Postcode or suburb");
  const [postcode, setPostCode] = useState("fw-bold text-decoration-underline");

  const hadlepostCode = () => {
    setIsRetailer(true);
    setRetailer("text-secondary");
    setInputLabel("Postcode or suburb");
    setPlaceHolder("post code or subhub");
    setPostCode("fw-bold text-decoration-underline")
  }

  const hadleReatailerName = () => {
    setIsRetailer(false);
    setPostCode("text-secondary");
    setInputLabel("Retailer name");
    setPlaceHolder("retailer name");
    setRetailer("fw-bold text-decoration-underline");
  }

  useEffect(() => {
    setIsRetailer(true);
  } , [])

  return (
    <>
      <div className='row site_body'>

        <h1 className='fw-bold' style={{ fontSize: "4rem" }}>Find a Retailer</h1>
        <p>We thank you for your time and support of an  Australian  <br /> designed, owned and made product.</p>

        <div className="col-md-6">

          <div className="d-flex">
            <div className={`me-5 ${postcode}`} onClick={hadlepostCode}>POSTCODE OR SUBURB</div>
            <div className={`${retailer}`} onClick={hadleReatailerName}>RETAILER NAME</div>
          </div>

          <div className="my-3">
            <Search inputLabel={inputlabel} placeholder={placeholder} />
          </div>

        </div>

        <div className="col-md-2 d-none d-md-block disabled"></div>

        <div className="col-md-4 d-none d-md-block">
          <div className="d-flex">

            <div className="vr me-3 px-1" style={{ background: "yellow" }}></div>

            {isretailer ?
              (<div className="">

                <h4>How to find a retailer :</h4>
                <ol>
                  <li> Enter an Australian Postcode or Suburb and select a location.</li>
                  <li>Select a Product Type from the available options.</li>
                  <li>Improve your search by using the additional filters.</li>
                </ol>

              </div>)
              :
              (<div className="">
                <h4>Search by Retailer Name :</h4>
                <ol>
                  <li> Enter the name of a business to check if they are a registered ZiptrakÂ® Retailer.</li>
                  <li>To improve your search, do not enter single words such as blind or awning.</li>
                </ol>
              </div>)
            }
          </div>
        </div>

      </div>

      <hr className='m-0 mt-5' />

      <div className='row g-0'>
        {isretailer ? (<MapSection />) : (<Map />)}
      </div>

    </>
  )
}

export default FindRetailer